@import "_vars";
@import "_logo";

.header {
  position: relative;
  display: flex;
  height: 765px;
  background-image: url(../images/header.jpg);
  background-size: cover;
  background-position: center;
  font-weight: 400;
  text-transform: uppercase;

  &__top {
    position: absolute;
    display: flex;
    flex-wrap: wrap;
    padding: 35px 0 0 calc(50% - #{$logo-width / 2});
    color: #fff;
  }

  &__nav {
    position: absolute;
    bottom: 25px;
    left: 0;
    right: 0;
  }

  &__phone {
    margin: 0 82px;
  }

  &__link {
    text-decoration: none;
    color: #fff;
    border-bottom: 1px dashed;
    transition: color $hover-duration;
  }

  &__link:hover {
    color: $active-color;
    transition: $hover-duration;
  }
}

@media screen and (max-width: $small-screen-breakpoint) {
  .header__top {
    flex-direction: column;
  }
}
